'use client'

import React, { useState, useEffect } from 'react'

interface CasinoGameProps {
    gameUrl: string
    width?: string
    height?: string
}

const CasinoGame: React.FC<CasinoGameProps> = ({
    gameUrl,
    width = '100%',
    height = '600px',
}) => {
    const [connectionError, setConnectionError] = useState(false)
    const [formattedUrl, setFormattedUrl] = useState('')

    useEffect(() => {
        // Ensure full URL for local development
        const normalizedUrl = gameUrl.startsWith('/')
            ? `${window.location.protocol}//${window.location.host}${gameUrl}`
            : gameUrl

        setFormattedUrl(normalizedUrl)

        // Attempt to fetch the URL to check connectivity
        fetch(normalizedUrl, { mode: 'no-cors' })
            .then(() => setConnectionError(false))
            .catch(() => {
                console.error('Failed to connect to game server')
                setConnectionError(true)
            })
    }, [gameUrl])

    if (connectionError) {
        return (
            <div className="bg-red-100 p-4 rounded">
                <h2 className="text-red-600 font-bold">
                    Game Server Connection Error
                </h2>
                <p>Unable to connect to the game server. Please check:</p>
                <ul className="list-disc pl-5">
                    <li>Is the game server running?</li>
                    <li>
                        Check server port (currently set to:{' '}
                        {new URL(formattedUrl).port})
                    </li>
                    <li>Verify no firewall is blocking the connection</li>
                    <li>Ensure CORS is properly configured</li>
                </ul>
                <p className="mt-2">Attempted URL: {formattedUrl}</p>
            </div>
        )
    }

    return (
        <iframe
            src={formattedUrl}
            width={width}
            height={height}
            allowFullScreen
            sandbox="allow-scripts allow-same-origin allow-top-navigation-by-user-activation allow-popups allow-forms"
            className="w-full"
        />
    )
}

export default CasinoGame
